body {
  background-image: url("logo.6718beec.jpg");
  background-size: 100%;
  height: 500vh;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body::-webkit-scrollbar {
  display: none;
}

.container {
  margin: 0 auto;
  position: relative;
  overflow-x: hidden;
}

header {
