.modal {
  z-index: 1001;
  background-color: #0006;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-title {
  text-align: center;
  width: 100%;
  margin-top: -40px;
  font-size: 22px;
}

.modal-content {
  background-color: #fefefe;
  border: 1px solid #888;
  border-radius: 5%;
  flex-direction: column;
  align-items: center;
  width: 30%;
  max-width: 300px;
  margin: 3% auto;
  padding: 15px;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
}

.modal-show {
  display: flex;
}

.modal-anim {
  animation: .3s linear forwards modal-show;
}

@keyframes modal-show {
  0% {
    transform: translate(-150%, -150%)rotate3d(0, 0, 1, 0);
  }

  50% {
    transform: translate(-100%, -125%)rotate3d(0, 0, 1, .5turn);
  }

  100% {
